<template>
  <section class="bg-white p-3 rounded-md">
    <PageTitleWithGoBackButton> Taqsimlangan bolalar </PageTitleWithGoBackButton>
    <TableView v-model:current-page="currentPage" v-model:page-size="pageSize" :page-size-options="pageSizeOptions" :all-pages-count="allPagesCount" @table-options-changed="fetchTeenagers">
      <template #filter-additional-content>
        <div class="grid grid-cols-2 md:grid-cols-4 gap-4 2xl:w-[60%]">
          <div class="form_wrapper my-3">
            <input v-validate-cyrillic id="search" v-model="search" placeholder="F.I.SH bo'yicha qidirish" class="base_input" name="search" type="text" />
          </div>
          <div class="form_wrapper my-3">
            <select id="address_region" v-model="address_region" name="address_region" class="base_input">
              <option :value="''">Barcha viloyatlar bo'yicha</option>
              <option v-for="region in regionsList" :key="region.id" :value="region.id">
                {{ region.name }}
              </option>
            </select>
          </div>
          <div class="form_wrapper my-3">
            <select v-model="school_type" name="school_type" class="base_input">
              <option :value="''">Barcha ta`lim muassasalari bo'yicha</option>
              <option v-for="t in schoolTypes" :key="t.id" :value="t.id">
                {{ t.text }}
              </option>
            </select>
          </div>
        </div>
      </template>
      <template v-if="loading" #loading>
        <Loading v-model:active="loading" :can-cancel="false" :is-full-page="false" color="#4785FE" loader="dots" />
      </template>
      <template v-else #table-content>
        <TableAllocatedJuveniles :table-body="teens" :view-for="'minors'" @sort-by-date="sortTeensByDate" />
      </template>
    </TableView>
  </section>
</template>

<script setup>
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import TableAllocatedJuveniles from '../components/Tables/TableAllocatedJuveniles';
import {ref, watch} from 'vue';
import TableView from '../components/Tables/TableView';
import useSchoolTypes from '../composables/selectbox-list/useSchoolTypes';
import useRegion from '../composables/selectbox-list/useRegion';
import useTableOptions from '../composables/useTableOptions';
import PageTitleWithGoBackButton from '../components/LayoutItems/PageTitleWithGoBackButton';
import AllocateService from '../services/allocate.service';
import useErrorAlert from '../composables/useErrorAlert';

const {fetchAllocatedJuvenilesList} = AllocateService;
const {regionsList} = useRegion();
const {schoolTypes} = useSchoolTypes();
const {currentPage, pageSize, allPagesCount, pageSizeOptions} = useTableOptions([10, 15, 20]);

const teens = ref([]);
const loading = ref(true);
const {alertThisError} = useErrorAlert();
const dateFilterType = ref('ASC' || 'DESC');
const school_type = ref('');
const address_region = ref('');
const search = ref('');

const fetchTeenagers = async (params) => {
  try {
    loading.value = true;
    const resp = await fetchAllocatedJuvenilesList({...params});
    teens.value = resp?.results;
    allPagesCount.value = resp?.num_pages;
    currentPage.value = resp?.current;
    loading.value = false;
  } catch (e) {
    alertThisError(e);
  }
};

watch(
  [search, school_type, address_region, pageSize, currentPage],
  async ([srch, stype, distrctId, psize, page]) => {
    await fetchTeenagers({
      full_name: srch,
      school_type: stype,
      address_region: distrctId,
      page_size: psize,
      page: page,
    });
  },
  {
    immediate: true,
  }
);

function sortTeensByDate() {
  if (dateFilterType.value === 'ASC') {
    dateFilterType.value = 'DESC';
    teens.value.sort(function (a, b) {
      return new Date(b.birth_date) - new Date(a.birth_date);
    });
  } else {
    dateFilterType.value = 'ASC';
    teens.value.sort(function (a, b) {
      return new Date(a.birth_date) - new Date(b.birth_date);
    });
  }
}
</script>
<style scoped></style>
