import axios from './api/axios';

const AllocateService = {
  async fetchAcceptedJuvenilesList(params = {}) {
    return (
      await axios.get('/api/juvenile/accepted/', {
        params: {
          ...params,
        },
      })
    ).data;
  },
  async fetchAllocatedJuvenilesList(params) {
    return (
      await axios.get('/api/juvenile/expelled/', {
        params: {
          ...params,
        },
      })
    ).data;
  },
  async fetchEmployedJuvenilesList(params) {
    return (
      await axios.get('/api/juvenile/employment/', {
        params: {
          ...params,
        },
      })
    ).data;
  },
  async fetchJuvenilesAllocateInfoById(id) {
    const {data} = await axios.get(`/api/juvenile/expelled_for_edit/${id}`);
    return data;
  },
  async editAllocatedJuvenile(formData, id) {
    return await axios.post(`/api/juvenile/juveniles/distribute_juvenile/`, formData, {
      params: {
        juvenile_id: id,
      },
    });
  },
  async sendJuvenileFromCenter(formData, id) {
    return await axios.post(`/api/juvenile/juveniles/distribute_juvenile/`, formData, {
      params: {
        juvenile_id: id,
      },
    });
  },
  async getAllocatedJuvenileInfo(id) {
    return await axios.get(`/api/juvenile/juveniles/${id}/send_juvenile/`);
  },
};
export default AllocateService;
